import React from 'react'
import CardItem from '../CardItem'
import '../Cards.css'
import myImage1 from "../../images/img-1.jpg"
import myImage2 from "../../images/img-1.jpg"

function FlexibilityCards() {
    return (
        <div className="cards">
            <h1> Read all the important facts about our impact on the planet!</h1>
            <div className="cards__container">
                <div className="cards__wrapper">
                    <ul className="cards__items">
                        <CardItem
                            src={myImage1}
                            text="Numbers and figures: where we are today"
                            label="Knowledge"
                        // path="/beginner-flex"
                        />
                        <CardItem
                            src={myImage2}
                            text="Recent research about plastic use"
                            label="Knowledge"
                        // path="/advanced-flex"
                        />
                    </ul>
                </div>
            </div>
        </div>
    )
}


export default FlexibilityCards;
